import React, { useState } from "react";
import {
  Alert,
  Box,
  IconButton,
  Snackbar,
  TextField,
  Typography,
} from "@mui/material";
import { useSelector } from "react-redux";
import traderwagon from "../../../assets/images/partners/traderwagon-black.svg";
import { CopyToClipboard } from "react-copy-to-clipboard";
import copyIcon from "../../../assets/images/state/document-copy.svg";

const Futures = ({ t }) => {
  const { profileData } = useSelector(
    ({ MyProfileReducer }) => MyProfileReducer
  );
  const data = profileData?.data?.profile;
  const [copy, setCopy] = useState(false);
  return (
    <Box
      sx={{
        boxShadow: "0px 1px 0px #E2E8F0",
        padding: { xs: 2, md: 4 },
        color: "#000",
        background: "#FFF",
        borderBottom: "1px solid #E7EAEE",
        mb: 3,
        borderRadius: "8px",
      }}
    >
      <Snackbar
        open={copy}
        autoHideDuration={2000}
        onClose={() => setCopy(false)}
        anchorOrigin={{ vertical: "top", horizontal: "center" }}
      >
        <Alert severity="success" color="success" variant="filled">
          {t("copied")}!
        </Alert>
      </Snackbar>

      <img
        src={traderwagon}
        style={{
          width: 200,
          height: 70,
          marginRight: 16,
        }}
        alt=""
      />
      <Typography
        variant="h6"
        sx={{
          fontWeight: "400",
          color: "#64748B",
          lineHeight: 1,
          borderTop: "1px solid #E7EAEE",
          pt: 2,
        }}
      >
        Используйте для подключения аккаунта Netex к TraderWagon
      </Typography>
      <Typography mt={3} color="#64748B" mb={0.5} fontWeight={700} variant="h6">
        API KEY:
      </Typography>
      <Box display="flex">
        <TextField
          fullWidth
          sx={{ "& .MuiOutlinedInput-root": { background: "#F7F8F9" }, mr: 2 }}
          value={data?.api_key}
        />
        <CopyToClipboard text={data?.api_key}>
          <IconButton onClick={() => setCopy(true)}>
            <img src={copyIcon} alt="" />
          </IconButton>
        </CopyToClipboard>
      </Box>

      <Typography color="#64748B" mt={3} mb={0.5} fontWeight={700} variant="h6">
        SECRET KEY:
      </Typography>
      <Box display="flex">
        <TextField
          sx={{ "& .MuiOutlinedInput-root": { background: "#F7F8F9" }, mr: 2 }}
          fullWidth
          value={data?.secret_key}
        />
        <CopyToClipboard text={data?.secret_key}>
          <IconButton onClick={() => setCopy(true)}>
            <img src={copyIcon} alt="" />
          </IconButton>
        </CopyToClipboard>
      </Box>
    </Box>
  );
};

export default Futures;
